 largo de la vida mediante el aprendizaje. Además, intervienen en simultáneo y generalmente se posee una tendencia hacia una por sobre las demás. Estas necesidades son: necesidad de logro o realización, necesidad de poder y necesidad de afiliación o social” (p.17).")]),_c('TabsC',{staticClass:"color-acento-contenido mb-5"},[_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Los clientes"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("El cliente es la persona más importante en una empresa, tanto el interno como el externo; es al cliente, hacia donde van enfocados todos los esfuerzos, creando e innovando nuevos productos. Por eso es importante diseñar estrategias para que el producto o servicio que se ofrece, cumpla con los requerimientos solicitados, buscando satisfacer las necesidades de los compradores.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-27.png")}})])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"Los empleados"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("Para ellos debe crearse un ambiente laboral que cumpla con las expectativas deseadas, dándoles empoderamiento para generar confianza y seguridad, así como también, bienestar y participación en la toma de decisiones de la organización, beneficiando a la empresa con una mayor productividad y rentabilidad económica.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-28.png")}})])])])]),_c('div',{staticClass:"py-3 py-md-4",attrs:{"titulo":"La sociedad"}},[_c('div',{staticClass:"row"},[_c('div',{staticClass:"col-md-6 mb-4 mb-md-0"},[_c('p',[_vm._v("La responsabilidad social que tienen los emprendedores frente a la sociedad, es la de satisfacer las necesidades individuales y colectivas, creando e innovando productos o servicios que cumplan las expectativas e