, para buscar una alternativa con su emprendimiento y que le permita crear un nuevo equilibrio y cambiar lo anticuado, buscando satisfacer una necesidad.")])]),_c('div',{staticClass:"col-md-6"},[_c('figure',[_c('img',{attrs:{"src":require("@/assets/curso/temas/tema1/img-35.png")}})])])])])],1)])],1)],1)}
